<script setup>
import { onMounted, onBeforeUnmount } from "vue";
// import { useRouter } from "vue-router";
import useBaseStore from "@/store/BaseStore";
import Videos from "../components/Videos.vue";
import Typing from "../components/Typing.vue";

const baseStore = useBaseStore();

const showModal = () => {
  baseStore.setModal(true);
  document.getElementsByTagName("html")[0].classList.add("unableToScroll");
};

// const router = useRouter();

const cardsServices = [
  {
    title: "Digital Marketing",
    subtitle: "TikTok, Youtube, Google, Facebook, Instagram and others",
    delay: 0,
  },
  {
    title: "Video Marketing",
    subtitle: "We increase sales through video promotion and brand integration",
    delay: 1,
  },
  {
    title: "Development",
    subtitle:
      "We create marketing strategies that unleash the potential of your brand to the maximum",
    delay: 2,
  },
  {
    title: "Websites",
    subtitle:
      "We bring the best in strategy, design & tech to help brands tell their story and connect to their customers",
    delay: 3,
  },
];
const cardsHowWeDoIt = [
  {
    title: "01",
    subtitles: ["Let's talk!", "We really want to know your business goals."],
    delay: 0,
  },
  {
    title: "02",
    subtitles: [
      "Evaluation!",
      "We optimize your budget as efficient as possible.",
    ],
    delay: 1,
  },
  {
    title: "03",
    subtitles: [
      "Let's find most cost-efficient approach to promotion of your business.",
    ],
    delay: 2,
  },
  {
    title: "04",
    subtitles: ["Numbers!", "We use data analytics in our creative approach!"],
    delay: 3,
  },
  {
    title: "05",
    subtitles: ["Start!", "Let's finally get new clients!"],
    delay: 4,
  },
];

onMounted(() => {
  window.addEventListener("scroll", updateScroll);
});

onBeforeUnmount(() => {
  window.removeEventListener("scroll", updateScroll);
});

const updateScroll = () => {
  const screeenHeight =
    window.innerHeight || document.documentElement.clientHeight;
  const onScrolledElements = document.getElementsByClassName("cards");
  const onScrolledElementsQuantity = onScrolledElements.length;
  Array(onScrolledElementsQuantity)
    .fill()
    .forEach((_, index) => {
      const currentElement = onScrolledElements[index];
      const distanceFromTop = currentElement.getBoundingClientRect().top;
      if (distanceFromTop <= screeenHeight / 2)
        currentElement.classList.add("show");
    });
};
</script>

