<template>
  <div class="typing" :class="{ typing_active: startTyping }" :id="setId">
    <span
      v-for="(symbol, index) in arrayString"
      :key="symbol"
      :style="`--delay: ${index};`"
      class="mk_90"
      :class="{space: isSpace(symbol)}"
    >
      {{ symbol }}
    </span>
  </div>
</template>

<script setup>
import { defineProps, toRefs, ref, onMounted, onBeforeUnmount } from "vue";

const props = defineProps({
  stringToType: String,
  setId: {
    type: String,
    required: true,
  },
});

const isSpace = (letter) => {
  return letter === '|';
};

const { stringToType, setId } = toRefs(props);

const arrayString = stringToType.value.split("");

const startTyping = ref(false);

const updateScroll = () => {
  const screeenHeight =
    window.innerHeight || document.documentElement.clientHeight;
  const onScrolledElement = document.getElementById(`${setId.value}`);
  const distanceFromTop = onScrolledElement.getBoundingClientRect().top;
  console.log({ screeenHeight, distanceFromTop });
  if (distanceFromTop <= screeenHeight / 2)
    onScrolledElement.classList.add("typing_active");
};

onMounted(() => {
  window.addEventListener("scroll", updateScroll);
  updateScroll();
});

onBeforeUnmount(() => {
  window.removeEventListener("scroll", updateScroll);
});
</script>

<style lang="scss">
.typing span {
  opacity: 0;
  transition-delay: calc(0.1s * var(--delay));

  &.space {
    opacity: 0;
  }
}

.typing_active span {
  opacity: 1;
}
</style>
